@import "~styles/common/normalize.scss";
@import "~styles/common/font_styles.scss";
@import "~styles/common/variables.scss";

body {
  font-family: 'Suisse-Regular', sans-serif;
  line-height: 1.5;
}
body.no-scroll{
  overflow: hidden;
  position: fixed;
  width: 100%;
}

section {
  padding: 80px 0;
  @media (max-width: 468px){
    padding: 60px 0;
  }
  @media (max-width: 360px){
    padding: 30px 0;
  }
}

.container {
  max-width: 1615px;
  width: 100%;
  padding: 0 60px;
  margin: 0 auto;

  @media (max-width: 468px){
    padding: 0 20px;
  }
}

/* Начало главной секции */

.hero {
  padding: 60px;
  display: flex;
  justify-content: space-between;
  gap: 150px;
  height: calc(100vh - 60px);

  &__left,
  &__right {
    max-width: 50%;
    width: 100%;
  }

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  h1 {
    font-size: clamp(40px, 4vw, 68px);
    line-height: 1.1;
    margin-bottom: 30px;
    color: var(--gray-text);

    span {
      color: var(--dark-text);
    }
  }

  &__btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 35px;
    padding: 6px 6px 6px 40px;
    background-color: var(--white);
    color: var(--dark-text);
    border: 1px solid #ececec;
    border-radius: 30px;
    overflow: hidden;
    position: relative;
    transition: background-color .2s, color .2s;

    span{
      padding: 15px;
      border-radius: 50%;
      width: 44px;
      height: 44px;
      background-color: var(--btn-gray);
      overflow: hidden;
      img {
        transform: translateX(0);
      }
    }

    &:hover{
      background-color: var(--dark-text);
      color: var(--white);
      transition: background-color .2s, color .2s;
      span{
        background-color: var(--white);
      }
    }
  }
  @keyframes arrowSlide {
    0% {
      transform: translateX(0);
      opacity: 1;
    }
    50% {
      transform: translateX(20px);
      opacity: 0.9;
    }
    51% {
      transform: translateX(-20px);
      opacity: 0.9;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }

  &__left-inner,
  &__socials {
    max-width: 610px;
    width: 100%;
    margin-left: auto;
  }

  &__socials-list {
    display: flex;
    align-items: center;
    gap: 60px;
  }

  &__socials-item a{
    position: relative;
    padding-left: 22px;
    img{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    span{
      border-bottom: 1px solid var(--dark-text);
      padding-bottom: 3px;
    }
    &:hover{
      font-weight: 500;
      span{
        border-bottom: 2px solid var(--dark-text);
      }
    }
  }

  &__compare {
    position: relative;
    max-width: 885px;
    max-height: 880px;
    width: 100%;
    overflow: hidden;
    user-select: none;
    border-radius: 60px;
    background-color: #eaeaea;
    z-index: 1;
  }

  &__compare-before,
  &__compare-after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  &__compare-before img,
  &__compare-after img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    user-select: none;
    -webkit-user-drag: none;
  }

  &__compare-after {
    clip-path: polygon(var(--clip-position, 50%) 0, 100% 0, 100% 100%, var(--clip-position, 50%) 100%);
  }

  &__compare-divider {
    position: absolute;
    top: 0;
    left: 50%;
    width: 2px;
    height: 100%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 10;
  }

  &__compare-arrow {
    position: absolute;
    font-size: 20px;
    width: 54px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--white);
    padding: 5px;
    border-radius: 50%;
  }

  &__compare-arrow img {
    -webkit-user-drag: none;
    user-select: none;
    pointer-events: none;
  }
}

.header {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo{
    position: relative;
    z-index: 3;
  }

  &__burger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 20px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    position: relative;
    z-index: 3;
  }

  &__burger span {
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--dark-text);
    transition: transform 0.3s ease, opacity 0.3s ease;
  }

  &__burger.active span:nth-child(1) {
    transform: rotate(45deg) translateY(8px) translateX(5px);
  }

  &__burger.active span:nth-child(2) {
    opacity: 0;
  }

  &__burger.active span:nth-child(3) {
    transform: rotate(-45deg) translateY(-8px) translateX(5px);
  }

  &__location {
    max-width: 120px;
    width: 100%;

    span {
      font-size: 12px;
      line-height: 20px;
    }
  }

  &__city {
    font-size: 16px;
    font-weight: 500;
  }

  &__menu-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 6px;
    grid-column-gap: 30px;
  }

  &__menu-item {
    max-width: 50%;
    width: 100%;
    font-size: 14px;
    line-height: 20px;

    .menu-text {
      position: relative;
      display: inline-block;
      overflow: hidden;

    }

    .menu-text span {
      position: relative;
      display: inline-block;
      color: transparent;

      &:before,
      &:after {
        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        transition: transform 2.5s cubic-bezier(0.19, 1, 0.22, 1);
        white-space: nowrap;
      }

      &:before {
        color: var(--dark-text);
        transform: skewY(-7deg) translateY(100%);
        transform-origin: right bottom;
      }

      &:after {
        color: var(--dark-text);
        transform: skewY(0) translateY(0);
      }
    }

    &:hover .menu-text span:before{
      color: var(--dark-text);
      transform: skewY(0) translateY(0);
    }

    &:hover span:after{
      transform: skewY(7deg) translateY(-100%);
    }
  }
}

@media (max-width: 1200px) {
  .hero {
    gap: 60px;
  }
}

@media (max-width: 1024px) {
  .header {
    &__location {
      display: none;
    }

    &__menu {
      position: fixed;
      top: 0;
      right: -300px; /* Начальное положение за пределами экрана */
      width: 350px;
      height: 100vh;
      padding: 20px;
      border-left: 1px solid #ececec;
      background-color: var(--white);
      box-shadow: var(--shadow);
      transition: right 0.3s ease, visibility 0.3s ease, opacity 0.3s ease;
      z-index: 2;
      visibility: hidden;
      opacity: 0;
    }

    &__menu.active {
      right: 0;
      visibility: visible;
      opacity: 1;
    }

    &__menu-list {
      grid-template-columns: 1fr;
      grid-row-gap: 10px;
    }

    &__menu-item  {
      max-width: 100%;
      a{
        display: block;
        width: 100%;
        font-size: 20px;
        line-height: 30px;
        padding: 10px 0;
      }
    }

    &__burger {
      display: flex;
    }
  }

  .hero{
    &__btn{
      gap: 25px;
      padding: 6px 6px 6px 15px;
    }
  }
}

@media (max-width: 768px) {
  .hero{
    height: auto;
    flex-direction: column;
    &__left{
      max-width: 100%;
      gap: 40px;
    }
    &__right{
      height: 350px;
    }
  }
}

@media (max-width: 468px) {
  .hero{
    padding: 20px 20px 60px;
    gap: 40px;
    h1{
      font-size: 32px;
    }

    .header{
      &__menu{
        width: 100%;
        padding-top: 80px;
      }
    }
  }
}

/* Конец главной секции */

/* Блок с движущимся текстом */

.logos {
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  display: flex;
  padding: 25px 0;
  border-top: 1px solid var(--dark-text);
  border-bottom: 1px solid var(--dark-text);

  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 0;
    width: 30%;
    height: 100%;
    z-index: 2;
  }

  &-slide {
    animation: 60s slide infinite linear;
    display: flex;
    gap: 160px;
    margin-right: 160px;
  }

  &-slide .ticker__item span {
    display: inline-block;
    font-size: clamp(32px, 4vw, 60px);
    line-height: 1.1;
  }
}

@keyframes slide {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

@media (max-width: 1024px){
  .logos-slide{
    gap: 120px;
  }
}

@media (max-width: 768px){
  .logos-slide{
    gap: 100px;
  }
}

@media (max-width: 468px){
  .logos-slide{
    gap: 60px;
  }
}

/* Блок с движущимся текстом */

/* Секция с услугами */

.services {
  &__header {
    display: flex;
    justify-content: space-between;
    gap: 125px;
    margin-bottom: 100px;
  }

  &__title {
    font-size: clamp(36px, 4vw, 60px);
    line-height: 1.1;
    max-width: 640px;
    width: 100%;
  }

  &__description {
    font-size: clamp(16px, 4vw, 24px);
    line-height: 1.6;
    max-width: 670px;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, auto);
    gap: 30px;
  }

  &__item {
    overflow: hidden;
    border-radius: 40px;

    img {
      border-radius: 40px;
    }

    &:hover{
      background-color: var(--white);
      box-shadow: var(--shadow);
      transition: box-shadow .3s, background-color .3s;

      .services__icon{
        transform: rotate(0deg);
        background-color: var(--dark-text);
        svg path{
          fill: var(--white);
        }
      }
    }
  }

  .div1 {
    grid-area: 1 / 1 / 2 / 2;
  }

  .div2 {
    grid-area: 1 / 2 / 3 / 3;
  }

  .div3 {
    grid-area: 2 / 1 / 4 / 2;
  }

  .div4 {
    grid-area: 3 / 2 / 4 / 3;
  }

  &__button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 46px 46px 40px;

    & > span {
      font-size: 30px;
      line-height: 1.3px;
    }

    .services__icon {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      padding: 19px;
      background-color: var(--btn-gray);
      transform: rotate(-45deg);
      transition: transform .3s;
    }

  }
}

@media (max-width: 1200px){
  .services{
    &__header{
      flex-direction: column;
      gap: 40px;
      margin-bottom: 75px;
    }
    &__title,
    &__description{
      max-width: 100%;
    }
  }
}

@media (max-width: 1024px){
  .services{
    &__button{
      &>span {
        font-size: 24px;
      }
      .services__icon{
        padding: 16px;
      }
    }
  }
}

@media (max-width: 860px){
  .services{
    &__inner{
      grid-template-columns: 1fr;
      grid-template-rows: auto;
    }
    &__item {
      width: 100%;
      grid-area: unset !important;
      img{
        max-height: 500px;
        width: 100%;
        object-fit: cover;
      }
    }
  }
}

@media (max-width: 468px){
  .services{
    &__header{
      gap: 25px;
      margin-bottom: 50px;
    }
    &__item img{
      max-height: 240px;
    }
    &__button{
      padding: 15px 16px 20px 15px;
      & > span{
        font-size: 20px;
      }
      .services__icon{
        padding: 14px;
      }
    }
  }
}

/* старт секции со слайдером*/

/* Контейнер секции */

.results{
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 2fr;
  width: 100%;
  padding: 60px 0;

  &__content{
    display: flex;
    padding-left: 15px;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    max-width: 427px;
    width: 100%;
    margin-left: auto;
  }
  &__text{
    max-width: 357px;
    width: 100%;
  }

  &__title{
    font-size: clamp(36px, 4vw, 60px);
    line-height: 1.1;
    margin-bottom: 30px;
  }
  &__text{
    font-size: clamp(16px, 4vw, 18px);
    line-height: 1.5;
  }

  &__controls{
    display: flex;
    gap: 41px;
    align-items: center;
    justify-content: start;
  }

  &__column--left{
    padding: 20px;
    border: 1px solid #ececec;
    border-radius: 0 50px 50px 0;
    z-index: 2;
  }

  &__column--right{
    z-index: 1;
    overflow: hidden;
    border: 1px solid #ececec;
    border-radius: 50px 0 0 50px;
  }

  .swiper-wrapper {
    display: flex;
    align-items: stretch;
  }

  .mySwiper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 30px;
  }

  .swiper-slide {
    overflow: hidden;
    border-radius: 40px;
    height: 760px;
    flex-shrink: 0;
  }

  .swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .slide-counter{
    font-size: 20px;
  }

  .swiper-button-next,
  .swiper-button-prev{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 17px;
    border-radius: 50%;
    background-color: var(--gray-bg);
    transition: all .3s;
    svg path{
      fill: var(--dark-text);
    }
    &.swiper-button-disabled{
      svg path{
        fill: #bbbbbb;
      }
      background-color: var(--gray-bg);
    }
    &:hover:not(.swiper-button-disabled){
      background-color: var(--dark-text);
      svg path{
        fill: var(--white);
      }
    }
  }
}

@media (max-width: 1024px){
  .results{
    .swiper-slide{
      height: 580px;
    }
  }
}

@media (max-width: 768px){
  .results{
    grid-template-columns: 1fr;

    &__column--left{
      padding: 50px 0;
      border-radius: 0 50px 0 0;
    }
    &__column--right {
      border-radius: 0 0 50px 0;
      padding: 20px;
    }
    &__content,
    &__text{
      max-width: 100%;
    }
    &__content{
      padding: 0 60px;
    }
    &__text{
      margin-bottom: 30px;
    }
  }
}

@media (max-width: 468px){
  .results{
    &__content{
      padding: 0 20px;
    }

    .mySwiper{
      padding: 30px 0;
    }

    .swiper-slide{
      height: 450px;
    }
  }
}

@media (max-width: 360px) {
  .results{
    .swiper-slide{
      height: 360px;
    }
  }
}
/* старт секции со слайдером*/

/* форма регистрации */

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
.off-canvas {
  position: fixed;
  top: 0;
  right: -100%;
  max-width: 945px;
  width: 100%;
  height: 100%;
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  gap: 100px;
  box-shadow: var(--shadow);
  transition: right 0.3s ease-in-out;
  z-index: 100;
  padding: 180px 120px;

  &.open {
    right: 0;
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;

    padding: 19px;
    background-color: var(--gray-bg);
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__title {
    font-size: clamp(36px, 4vw, 60px);
    line-height: 1.1;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 50px;
  }

  &__label {
    font-size: 16px;
  }

  &__input {
    padding: 10px;
    font-size: clamp(16px, 4vw, 24px);
    line-height: 1.2;
    border: none;
    border-bottom: 1px solid #ececec;
  }

  &__submit {
    font-size: clamp(16px, 4vw, 24px);
    padding: 45px 65px;
    width: max-content;
    color: var(--dark-text);

    border: 1px solid var(--dark-text);
    border-radius: 60px;
    cursor: pointer;
    transition: all .3s;

    &:hover{
      background-color: var(--dark-text);
      color: var(--white);
    }
  }

  &__policy {
    margin-top: 20px;
    font-size: 14px;
    color: #c4c4c4;
  }
}

.off-canvas__policy a:hover {
  text-decoration: underline;
}

@media (max-width: 1650px) {
  .off-canvas {
    max-width: 500px;
    gap: 50px;
    box-shadow: var(--shadow);
    transition: right 0.3s ease-in-out;
    z-index: 100;
    padding: 40px 50px;

    &__submit{
      padding: 20px 50px;
    }
  }
}

@media (max-width: 568px) {
  .off-canvas{
    padding: 75px 25px;
  }
}

/* форма регистрации */